export default function () {
    return new Promise((resolve, reject) => {
        uni.request({
            url: 'https://www.example.com/request', //
            data: {
                text: 'uni.request'
            },
            header: {
                'custom-header': 'hello' //
            },
            success: (res) => {
                resolve(res)
            },
            fail(){
                reject(res)
            }
        });
    })

}